import {mapState, mapGetters} from 'vuex';
import {uniCard, uniIcons} from '@dcloudio/uni-ui';
import collapse from '../../components/collapse';
import mpvuePicker from 'mpvue-picker';
export default {
  props: {},
  components: {
    uniCard,
    collapse,
    uniIcons,
    mpvuePicker,
  },
  computed: {
    ...mapState('detail', {
      app: state => {
        return state.app;
      },
      allStatus: state => {
        return state.status;
      },
      pro: state => {
        return state.pro;
      },
      ins: state => {
        return state.ins;
      },
    }),
    ...mapGetters('detail', ['doc', 'proIns']),
    enablePicker() {
      return this.$store.state.detail.docs.length > 1;
    },
    status() {
      return this.doc ? this.doc.status.name : '...';
    },
    statusColor() {
      let color = uni.$color.carrot;
      switch (this.status) {
        case 'ongoing':
          color = uni.$color.orange;
          break;
        case 'owner_confirm':
          color = uni.$color.carrot;
          break;
        case 'passed':
          color = uni.$color.nephritis;
          break;
        case 'failed':
          color = uni.$color.pomegranate;
          break;
      }
      return color;
    },
  },
  watch: {},
  data() {
    return {
      pickerVal: [0, 0],
    };
  },
  methods: {
    pickerProInsShow(e) {
      this.$refs.pickerProIns.show();
    },
    pickerProInsOnChange(e) {},
    pickerProInsOnCancel(e) {},
    pickerProInsOnConfirm(e) {
      let pro = this.proIns[e.index[0]].label;
      let ins = this.proIns[e.index[0]].children[e.index[1]].label;
      this.$store.commit('detail/setPro', pro);
      this.$store.commit('detail/setIns', ins);
    },
  },
};
